import React from "react";
import Former from "./Former";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

class FeedbackComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hide: false,
      sended:false
    };
  }
  toggleHide() {
    this.setState({ hide: !this.state.hide });
    
  }
  toastCallback() {
    toast.success("Заявка отправлена", {
      position: toast.POSITION.BOTTOM_LEFT,
      onOpen: () => this.setState({sended:true}),
      onClose: () => this.setState({sended:false})
    });
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.currentPage !== 4) {
      this.setState({ hide: false });
    }
  }

  render() {
    const { hide,sended } = this.state;
    return (
      <div
        className={
          hide
            ? "full component last-component half-component"
            : "component last-component half-component"
        }
      >
        <div className={hide ? "half-visible hide" : "half-visible"}>
          {!sended?<img
            onClick={() => {
              this.toggleHide();
            }}
            src={require("../assets/img/meme.png")}
            alt="2"
          />: <h2>за вами выехали</h2>}
        </div>
        <div className={hide ? "half-hidden visible" : "half-hidden"}>
          <Former
            toggler={() => this.toggleHide()}
            callback={() => this.toastCallback()}
          />
        </div>
        <ToastContainer />
      </div>
    );
  }
}
export default FeedbackComponent;
