import React from "react";
import { Button, UncontrolledTooltip } from "reactstrap";
import Lightbox from "react-image-lightbox";

const images = [
  require('../assets/img/full1.png'),
  require('../assets/img/full2.png'),
  require('../assets/img/full3.png'),
  require('../assets/img/full4.png'),
];

class HalfedComponentOne extends React.Component {
  constructor() {
    super();
    this.state = {
      hide: false,
      lightbox: false,
      photoIndex: 0
    };
  }
  toggleHide() {
    this.setState({ hide: !this.state.hide });
  }
 

  render() {
    const { hide, lightbox, photoIndex } = this.state;
    return (
      <div
        className={
          hide
            ? "full component second-component half-component"
            : "component second-component half-component"
        }
      >
        <div className={hide ? "half-visible hide" : "half-visible"}>
          <img
            onClick={() => {
              this.toggleHide();
            }}
            src={require("../assets/img/sample3.png")}
            alt="2"
          />
          <h3>Вот так на аймаке</h3>
          <Button
            onClick={() => {
              this.toggleHide();
            }}
          >
            а еще так
          </Button>
        </div>
        <div className={hide ? "half-hidden visible" : "half-hidden"}>
          <div className="hide-wrap">
          <div 
              className="hide-back"
              onClick={() => {
                this.toggleHide();
              }}
            >
              ◄  
            </div>
            <div className="hide-viewer">
              <img
                id="tooltip1"
                onClick={() => this.setState({ lightbox: true })}
                src={require("../assets/img/full2.png")}
                alt="2"
              />
              <UncontrolledTooltip placement="auto" target="tooltip1">
                Вот такая опция
              </UncontrolledTooltip>
              <p className={!hide ? "p-hide" : null}>
                Далеко-далеко за словесными горами в стране гласных, и согласных
                живут рыбные тексты. Раз  жизни однажды ему, путь своих
                там живет ведущими эта.
              </p>

              {lightbox && (
                <Lightbox
                  mainSrc={images[photoIndex]}
                  nextSrc={images[(photoIndex + 1) % images.length]}
                  prevSrc={
                    images[(photoIndex + images.length - 1) % images.length]
                  }
                  onCloseRequest={() => this.setState({ lightbox: false })}
                  onMovePrevRequest={() =>
                    this.setState({
                      photoIndex:
                        (photoIndex + images.length - 1) % images.length
                    })
                  }
                  onMoveNextRequest={() =>
                    this.setState({
                      photoIndex: (photoIndex + 1) % images.length
                    })
                  }
                />
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default HalfedComponentOne;
