import React from 'react';

import { Pager } from "react-bootstrap";
import ReactPageScroller from "react-page-scroller";
import FirstComponent from "./components/FirstComponent";
import HalfedComponentOne from "./components/HalfedComponentOne";
import HalfedComponentTwo from "./components/HalfedComponentTwo";
import HalfedComponentThird from "./components/HalfedComponentThird";
import FeedbackComponent from "./components/FeedbackComponent";


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { currentPage: null };
  }
  handlePageChange = number => {
    this.setState({ currentPage: number });
  };
  getPagesNumbers = () => {
    const names = ['Главная', 'iMac', 'iPad', 'iPhone','Хочу'], pageNumbers = []

    for (let i = 1; i <= 5; i++) {
      pageNumbers.push(
        <Pager.Item key={i} eventKey={i - 1} onSelect={this.handlePageChange}>
          {names[i - 1]}
        </Pager.Item>,
      );
    }

    return [...pageNumbers];
  };
  render() {
    const {currentPage} = this.state
    const pagesNumbers = this.getPagesNumbers();
    return (
      <>
        <ReactPageScroller
         
          pageOnChange={this.handlePageChange}
          customPageNumber={this.state.currentPage}
        >
          <FirstComponent />
          <HalfedComponentOne />
          <HalfedComponentTwo />
          <HalfedComponentThird />
          <FeedbackComponent currentPage={currentPage }/>

        </ReactPageScroller>
        <Pager className="pagination-additional-class" bsSize="large">
          {pagesNumbers}
        </Pager>
      </>

    )
  }

}

export default App;
