import React from "react";
import {
  Button,
  Form,
  FormGroup,
  Label,
  Input 
} from "reactstrap";

class Former extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        sended:false,
        toggler:props.toggler,
        callback:props.callback
    };
  }
  sendMsg() {
      this.setState({sended:true})
      this.state.toggler() 
      this.state.callback()

  }
  render() {
    const {sended} = this.state  
    return (
      <div className="form-wrap">
        <Form>
          <FormGroup>
            <Label for="exampleEmail">Email</Label>
            <Input
              type="email"
              name="email"
              id="exampleEmail"
              placeholder="Ваш email"
            />
          </FormGroup>
          <FormGroup>
            <Label for="exampleEmail">Имя</Label>
            <Input type="text" name="name" id="name" placeholder="Ваше Имя" />
          </FormGroup>
          <FormGroup>
            <Label for="examplePassword">Телефон</Label>
            <Input
              type="number"
              name="phone"
              id="phone"
              placeholder="Ваш номер телефона"
            />
          </FormGroup>

          <Button
            onClick={() => {
              this.sendMsg();
            }}
          >
            Хочу 
          </Button>
        </Form> 
      </div>
    );
  }
}

export default Former;
